/* ---------------------------
Variables
------------------------------ */

// colors
$black: #1A1A1A;
$dark: #3c434c;
$grey: #828282;
$grey2: #ABABAB;
$light: #E9E9E9;
$white: #FFFFFF;
$link: $dark;

// fonts
$font-serif: Georgia, Times, serif;
$font-sans-serif: Arial, Helvetica, sans-serif;
$font-weight-thin: 100;
$font-weight-light: 300;
$font-weight-regular: 400;
$font-weight-heavy: 700;
$font-height: 1.7;
$font-size: 18px;
$font-size-large: 24px;
$font-size-extralarge: 3rem;

// grid
$width: 96%;
$gutter: 4%;
$breakpoint-small: 33.75em; // 540px
$breakpoint-med: 45em; // 720px
$breakpoint-large: 60em; // 960px


/* ---------------------------
12 Grid System
------------------------------ */
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
}

.container {
  width: 90%;
  max-width: 45rem;	
  margin-left: auto;
  margin-right: auto;

  @media (max-width: $breakpoint-large) {
  	font-size: $font-size-large;
  }
}

.row {
  position: relative;
  width: 100%;
}

.row [class^="col"] {
  float: left;
  margin: 0.5rem 2%;
  min-height: 0.125rem;
}

.row::after {
  content: "";
  display: table;
  clear: both;
}

.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12 {
  width: $width;
}

.col-1-sm { width:($width / 12) - ($gutter * 11 / 12); }
.col-2-sm { width: ($width / 6) - ($gutter * 10 / 12); }
.col-3-sm { width: ($width / 4) - ($gutter * 9 / 12); }
.col-4-sm { width: ($width / 3) - ($gutter * 8 / 12); }
.col-5-sm { width: ($width / (12 / 5)) - ($gutter * 7 / 12); }
.col-6-sm { width: ($width / 2) - ($gutter * 6 / 12); }
.col-7-sm { width: ($width / (12 / 7)) - ($gutter * 5 / 12); }
.col-8-sm { width: ($width / (12 / 8)) - ($gutter * 4 / 12); }
.col-9-sm { width: ($width / (12 / 9)) - ($gutter * 3 / 12); }
.col-10-sm { width: ($width / (12 / 10)) - ($gutter * 2 / 12); }
.col-11-sm { width: ($width / (12 / 11)) - ($gutter * 1 / 12); }
.col-12-sm { width: $width; }

@media only screen and (min-width: $breakpoint-med) {
  .col-1 { width:($width / 12) - ($gutter * 11 / 12); }
  .col-2 { width: ($width / 6) - ($gutter * 10 / 12); }
  .col-3 { width: ($width / 4) - ($gutter * 9 / 12); }
  .col-4 { width: ($width / 3) - ($gutter * 8 / 12); }
  .col-5 { width: ($width / (12 / 5)) - ($gutter * 7 / 12); }
  .col-6 { width: ($width / 2) - ($gutter * 6 / 12); }
  .col-7 { width: ($width / (12 / 7)) - ($gutter * 5 / 12); }
  .col-8 { width: ($width / (12 / 8)) - ($gutter * 4 / 12); }
  .col-9 { width: ($width / (12 / 9)) - ($gutter * 3 / 12); }
  .col-10 { width: ($width / (12 / 10)) - ($gutter * 2 / 12); }
  .col-11 { width: ($width / (12 / 11)) - ($gutter * 1 / 12); }
  .col-12 { width: $width; }

  .hidden-sm {
    display: block;
  }
}



/* ---------------------------
Layout
------------------------------ */
header, footer {
	padding: 4rem 0;
	font-family: $font-sans-serif;
}

footer {
	margin-top: 2rem;
	color: $grey;

	a {
		color: $grey;
		margin-right: 10px;
	}

	nav {
		margin: 2rem 0;
	}
}

nav {
	margin: 1rem 0;

	a {
		margin-right: 10px;
		text-decoration: none;

		&:hover {
			color: $black;
		}
	}
}



/* ---------------------------
Typography
------------------------------ */
* {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

html, body {
	font-size: $font-size;
	font-family: $font-serif;
	color: $black;
	line-height: $font-height;
}

h1, h2, h3, h4, h5 {
	font-family: $font-sans-serif;
}

h1 {
	font-size: 2.125rem;
	letter-spacing: -0.05rem;
	line-height: 3rem;
}

h1.slim {
	font-weight: $font-weight-thin;

}
h2 {font-size: 2rem;}
h3 {font-size: 1.375rem;}
h4 {font-size: 1.125rem;}
h5 {font-size: 1rem;}

p {
	font-weight: $font-weight-regular;
	line-height: $font-height;
}

a {
	color: $link;

	&:hover {
		color: $dark;
	}
}

blockquote {
	border-left: 5px solid $dark;
	margin-top: 3rem;
	margin-bottom: 3rem;
	margin-left: 0;
	padding-left: 3rem;
	font-size: 1.3rem;
	font-family: Arial;
	text-align: left;
	font-weight: $font-weight-heavy;
}

.font-light {font-weight: $font-weight-light;}
.font-regular {font-weight: $font-weight-regular;}
.font-heavy {font-weight: $font-weight-heavy;}

.text-large {font-size: 1.2rem;}
.text-small {font-size: 0.9rem;}
.text-smaller {font-size: 0.8rem;}

.clean-links {
	a {
		text-decoration: none;
	}
}

.text-grey, .text-grey a {
	color: $grey;
}



